<template>

    <div>
        <div class="contain">


            <div id='particles-js'>


            </div>


        </div>

    </div>
</template>

<script>
    export default {
        name: 'ParticlesJS',

        mounted() {
            require('particles.js');
            this.$nextTick(() => {
                this.initParticlesJS()
            })
        },

        methods: {
            initParticlesJS() {
                /* eslint-disable */
                particlesJS('particles-js',

                    {
                        "particles": {
                            "number": {
                                "value": 100,
                                "density": {
                                    "enable": true,
                                    "value_area": 800
                                }
                            },
                            "color": {
                                "value": "#000000"
                            },
                            "shape": {
                                "type": "circle",
                                "stroke": {
                                    "width": 1,
                                    "color": "#000000"
                                },
                                "polygon": {
                                    "nb_sides": 6
                                },
                                "image": {
                                    "src": "img/github.svg",
                                    "width": 100,
                                    "height": 100
                                }
                            },
                            "opacity": {
                                "value": 0.5,
                                "random": false,
                                "anim": {
                                    "enable": false,
                                    "speed": 1,
                                    "opacity_min": 0.1,
                                    "sync": false
                                }
                            },
                            "size": {
                                "value": 3,
                                "random": true,
                                "anim": {
                                    "enable": false,
                                    "speed": 40,
                                    "size_min": 0.1,
                                    "sync": false
                                }
                            },
                            "line_linked": {
                                "enable": true,
                                "distance": 150,
                                "color": "#000000",
                                "opacity": 1,
                                "width": 0.5
                            },
                            "move": {
                                "enable": true,
                                "speed": 6.409588744762158,
                                "direction": "none",
                                "random": false,
                                "straight": false,
                                "out_mode": "bounce",
                                "bounce": false,
                                "attract": {
                                    "enable": false,
                                    "rotateX": 600,
                                    "rotateY": 1200
                                }
                            }
                        },
                        "interactivity": {
                            "detect_on": "canvas",
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "grab"
                                },
                                "onclick": {
                                    "enable": true,
                                    "mode": "repulse"
                                },
                                "resize": true
                            },
                            "modes": {
                                "grab": {
                                    "distance": 400,
                                    "line_linked": {
                                        "opacity": 1
                                    }
                                },
                                "bubble": {
                                    "distance": 400,
                                    "size": 40,
                                    "duration": 2,
                                    "opacity": 8,
                                    "speed": 3
                                },
                                "repulse": {
                                    "distance": 200,
                                    "duration": 0.4
                                },
                                "push": {
                                    "particles_nb": 4
                                },
                                "remove": {
                                    "particles_nb": 2
                                }
                            }
                        },
                        "retina_detect": true
                    }
                );
            }
        }

    }
</script>

<style>
    /* Your styles */


    /*svkshbvlsdv */


    .contain {
        background-color: transparent;


    }

    #particles-js {
        position: absolute;
        width: 100%;
            height: 90vh;
        z-index: -1;
    }


    @media only screen and (max-width: 767px){

        #particles-js {
            position: absolute;
            width: 100%;
            height: 100vw;
            z-index: -1;
        }
    }

    @media all and (orientation:portrait) {
        /* Style adjustments for portrait mode goes here */
        #particles-js {
            position: absolute;
            width: 100%;
            height: 100vw;
            z-index: -1;
        }
    }


</style>